.video {
  height: 500px;
  padding-right: 260px;
  padding-left: 165px;
  padding-top: 94px;
  /* Mobile */
  @media (max-width: 899px) {
    padding: 0;
    padding-right: 10px;
    padding-left: 10px;
    padding-top: 90px;
  }
}

.container {
  display: flex;
  justify-content: space-between;
  max-width: 1100px;
  margin: auto;
  /* Mobile */
  @media (max-width: 899px) {
    display: block;
  }
}
.title {
  width: 445px;
  margin-right: 125px;
  /* Mobile */
  @media (max-width: 899px) {
    width: 100%;
  }
  & h2 {
    color: #000;
    font-family: Lato;
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 24px;
    /* Mobile */
    @media (max-width: 899px) {
      font-size: 26px;
      text-align: center;
    }
  }
  & p {
    color: #000;
    text-align: justify;
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19.6px;
    margin-bottom: 15px;
  }

  & a {
    color: #ed4c5c;
    text-align: justify;
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19.6px;
    text-decoration: none;
  }
}

.img {
  position: relative;
  max-width: 445px;
  /* Mobile */
  @media (max-width: 899px) {
     padding-top: 20px;
  }
  & svg {
    position: absolute;
    top: 120px;
    left: -30px;
    /* Mobile */
    @media (max-width: 899px) {
      display: none;
    }
  }
  & img {
    width: 100%;
  }
}
