.technology {
  padding-top: 63px;
  padding-bottom: 124px;
}

.continer {
  max-width: 1100px;
  margin: auto;
  & h2 {
    margin: 0;
    margin-bottom: 52px;

    color: #000;
    text-align: center;
    font-family: Lato;
    font-size: 36px;
    font-weight: 600;
  }

}

.items {
  display: flex;
  justify-content: space-between;
  /* Mobile */
  @media (max-width: 899px) {
     display: block;
  }

  & h1 {
    color: rgba(16, 16, 16, 0.08);
    text-align: center;
    font-family: Lato;
    font-size: 100px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin: 0;
  }

  & div {
    position: relative;
    width: 255px;
    /* Mobile */
    @media (max-width: 899px) {
       margin-left: auto;
       margin-right: auto;
    }
  }
  & svg {
    position: absolute;
    top: 32%;
    left: 34%;
  }

  & h3 {
    color: #101010;
    text-align: center;
    font-family: Lato;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }

  & p {
    width: 190px;
    color: #000;
    text-align: center;
    font-family: Lato;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: auto;
  }
}
