.conteiner {
  max-width: 1440px;
  margin: auto;
}
.main {
  background-image: url("../../img/Bg.png");
  background-repeat: no-repeat;
  height: 700px;
  background-size: cover;
  background-position: 100%;
  /* Mobile */
  @media (max-width: 899px) {
    height: 520px;
  }
}
.nav {
  display: flex;
  justify-content: space-between;
  padding-left: 166px;
  padding-right: 166px;
  padding-top: 50px;
  /* Mobile */
  @media (max-width: 899px) {
    padding: 0;
    display: block;
  }
}
.logo {
  display: flex;
  & p {
    color: #fff;
    font-family: Lato;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-transform: uppercase;
    padding-top: 13px;
    /* Mobile */
    @media (max-width: 899px) {
      padding-top: 5px;
    }
  }
  & svg {
    margin: auto;
    /* Mobile */
    @media (max-width: 899px) {
      margin: 0;
    }
  }
}
.list {
  display: flex;
  list-style-type: none;
  margin-top: 20px;
  & a {
    margin-right: 20px;
    color: #fff;
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
    text-decoration: none;
    &:hover {
      color: #593a3a;
      transition: 0.4s;
    }
    /* Mobile */
    @media (max-width: 899px) {
      margin-right: 6px;
    }
  }
  /* Mobile */
  @media (max-width: 899px) {
    margin-top: 5px;
  }
}
.button {
  display: block;
  color: #fff;
  text-align: center;
  font-family: Lato;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  margin-top: 5px;
  background-color: transparent;
  padding: 11px 29px;
  border-radius: 6px;
  border: 1px solid #fff;
  text-decoration: none;
  &:hover {
    color: #593a3a;
    border: 1px solid #593a3a;
    transition: 0.7s;
    background-color: #fff;
  }
  /* Mobile */
  @media (max-width: 899px) {
    width: 100px;
    margin-left: auto;
    margin-right: auto;
    padding: 5px 10px;
  }
}
.title {
  width: 580px;
  margin: 140px 0px 0px 0px;
  margin-left: auto;
  margin-right: auto;
  /* Mobile */
  @media (max-width: 899px) {
    width: 385px;
    margin: 40px 0px 0px 0px;
  }
  & h1 {
    color: #fff;
    font-family: Lato;
    font-size: 72px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 3.6px;
    text-transform: uppercase;
    margin: 0;
    /* Mobile */
    @media (max-width: 899px) {
      font-size: 40px;
      text-align: center;
    }
  }
  & p {
    margin: 0;
    margin-top: 40px;
    color: #fff;
    text-align: center;
    font-family: Lato;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 36px;
  }
  & a {
    display: block;
    width: 60px;
    margin: auto;
    margin-top: 40px;
    color: #ed4c5c;
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-transform: uppercase;

    padding: 17px 53px;
    border-radius: 6px;
    background: #fff;
    text-decoration: none;
    border: 1px solid #fff;
    &:hover {
      transition: 0.7s;
      background-color: transparent;
      color: #fff;
      border: 1px solid #fff;
    }
  }
}
