.features {
  height: 660px;
  /* Mobile */
  @media (max-width: 899px) {
    height: 1200px;
    margin-top: 90px;
  }
}

.title {
  color: #000;
  text-align: center;
  font-family: Lato;
  font-size: 36px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-bottom: 62px;
  /* Mobile */
  @media (max-width: 899px) {
    width: 100%;
  }
}

.item {
  position: relative;
  /* Mobile */
  @media (max-width: 899px) {
    margin-bottom: 120px;
margin-left: 15px;
  }

  & div {
    border-radius: 8px;
    background: #fff;
    box-shadow: 0px 5px 30px 0px rgba(0, 0, 0, 0.1);
    position: absolute;
    top: 150px;
    left: 28px;
    width: 227px;
    padding: 33px 32px 25px;
    margin: 0;
    & h3 {
      margin: 0;
      color: #101010;
      text-align: center;
      font-family: Lato;
      font-size: 18px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      margin-bottom: 20px;
    }
    & p {
      margin: 0;
      color: #101010;
      text-align: center;
      font-family: Lato;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      margin-bottom: 5px;
    }
    & a {
      margin: 0;
      color: #ed4c5c;
      text-align: center;
      font-family: Lato;
      font-size: 12px;
      font-style: normal;
      font-weight: 700;
      line-height: 18px;
      text-decoration: none;
    }
  }
}

.container {
  max-width: 1110px;
  margin: auto;
}

.items {
  display: flex;
  justify-content: space-between;
  /* Mobile */
  @media (max-width: 899px) {
    display: block;
  }
}
