*img {
  /* Mobile */
  @media (max-width: 899px) {
    width: 100%;
  }
}

.download {
  background-color: #ed4c5c;
  padding-top: 94px;
  padding-bottom: 94px;
}
.container {
  max-width: 1070px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  /* Mobile */
  @media (max-width: 899px) {
    display: block;
    padding-left: 10px;
    padding-right: 10px;
  }

  & div {
    width: 445px;
    /* Mobile */
    @media (max-width: 899px) {
      width: 100%;
      margin-bottom: 20px;
    }

    & h3 {
      color: #fff;
      font-family: Lato;
      font-size: 36px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      margin: 0;
      margin-bottom: 24px;
      /* Mobile */
      @media (max-width: 899px) {
        text-align: center;
      }
    }

    & p {
      color: #fff;
      text-align: justify;
      font-family: Lato;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 19.6px;
      margin: 0;
      margin-bottom: 40px;
    }

    & a {
      margin-right: 20px;
      &:hover {
        opacity: 0.4;
        transition: 0.4s;
      }
    }
  }
}
