* {
  margin: 0;
}

.contact {
  padding-top: 118px;
  padding-bottom: 78px;
  margin: 0;
  /* Mobile */
  @media (max-width: 899px) {
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 58px;
    padding-bottom: 40px;
  }
}
.container {
  max-width: 1100px;
  margin: auto;

  display: flex;
  justify-content: space-between;
  /* Mobile */
  @media (max-width: 899px) {
    display: block;
    width: 100%;
  }
}

.title {
  width: 434px;
  /* Mobile */
  @media (max-width: 899px) {
    width: 100%;
    margin-bottom: 20px;
  }
  & h2 {
    color: #000;
    font-family: Lato;
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 60px;
    /* Mobile */
    @media (max-width: 899px) {
      width: 100%;
      margin-bottom: 20px;
    }
  }

  & p {
    color: #000;
    text-align: justify;
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19.6px;
  }
}

.name {
  margin-bottom: 24px;
  /* Mobile */
  @media (max-width: 899px) {
    margin-bottom: 0px;
  }
  & input {
    color: rgba(19, 39, 67, 0.5);
    font-family: Lato;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    border-radius: 6px;
    border: 1px solid var(--gray-1, #333);
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    margin-right: 30px;
    /* Mobile */
    @media (max-width: 899px) {
      margin-bottom: 10px;
      width: 90%;
    }
  }
}

.phone {
  margin-bottom: 24px;
  /* Mobile */
  @media (max-width: 899px) {
    margin-bottom: 10px;
  }
  & input {
    width: 90%;
    color: rgba(19, 39, 67, 0.5);
    font-family: Lato;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    border-radius: 6px;
    border: 1px solid var(--gray-1, #333);
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
  }
}

.services {
  margin-bottom: 27px;
  & select {
    width: 93%;
    color: rgba(19, 39, 67, 0.5);
    font-family: Lato;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    border-radius: 6px;
    border: 1px solid var(--gray-1, #333);
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
  }
}

.submit {
  & input {
    border-radius: 6px;
    border: 1.5px solid #ed4c5c;
    background: #ed4c5c;
    padding: 17px 34px;

    color: #fff;
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
    /* Mobile */
    @media (max-width: 899px) {
      padding: 12px 25px;
    }
    &:active {
      background: #c40606;
      transition: 0.1s;
      border: 1.5px solid #c40606;
    }
  }
}
