* {
  margin: 0;
  padding: 0;
}

.footer {
  padding-top: 85px;
  padding-bottom: 80px;
  background: #fafafa;
  /* Mobile */
  @media (max-width: 899px) {
    padding-top: 45px;
    padding-bottom: 40px;
 }
}

.container {
  max-width: 1100px;
  margin: auto;
  /* Mobile */
  @media (max-width: 899px) {
     padding-left: 10px;
     padding-right: 10px;
  }

}

.contact {
  display: flex;
  justify-content: space-between;
}

.address {
  width: 254px;
  /* Mobile */
  @media (max-width: 899px) {
     width: 95px;
  }
  & h3 {
    color: #101010;
    font-family: Lato;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 33px;
  }
  & p {
    color: #101010;
    font-family: Lato;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 18px */
  }
}

.services {
  & h3 {
    color: #101010;
    font-family: Lato;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 28px;
  }
  & li {
    list-style-type: none;
    color: #000;
    font-family: Lato;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%; /* 19.2px */
    text-transform: capitalize;
  }

  & a {
    position: relative;
    text-decoration: none;

    &::after {
      display: block;
      position: absolute;
      left: 0;
      width: 0;
      height: 2px;
      background-color: #080808; /*задаём цвет линии*/
      content: "";
      transition: width 0.4s ease-out; /*задаём время анимации*/
    }

    &:hover::after {
      width: 100%;
    }
  }
}

.touch {
  & h3 {
    color: #101010;
    text-align: center;
    font-family: Lato;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 33px;
  }

  & p {
    color: #101010;
    font-family: Lato;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 11px;
  }
  & svg {
    margin-right: 12px;
  }
}
